    margin: 0.1rem auto;
  }
.supervipnumbe{
  width: 92%;
  margin:0.5rem auto 0;
  font-size:0.18rem;

}
  .supervipnumber{
    width: 100%;
    height: 300%;
    background: white;
    font-family:"";

  }
  img{
    width: 100%;
